import { button } from 'bootstrap';
import {Table, Navbar, Nav, NavDropdown, Form, FormControl, Container,Button} from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { changeName,increAge,decreAge,함수2,함수3 } from '../store/userSlice';
import { increment,decrement,delItem } from '../store.js';

function Cart() {
    //let user = useSelector((state) => {return state.user}); 
    let user = useSelector(state => state.user); // 축약형
    let dispatch = useDispatch(); // dispatch : store.js로 메시지 요청하는 함수

    let counter = useSelector((state) => {return state.counter});
    let allstate = useSelector((state) => {return state});
    let stock = useSelector((state) => {return state.stock});
    // use  Selector((state) => {return state.cart}); 
    // cart reducer     
    console.log('@@ Cart.js useSelector(state) uesr=',user);
    console.log('@@ Cart.js useSelector(state) counter=',counter);
    console.log('@@ Cart.js useSelector(state) allstate=',allstate);
    console.log('@@ Cart.js useSelector(state) stock=',stock);

    let cart = useSelector((state) => {return state.cart});
    console.log('@@ Cart.js useSelector(state) cart=',cart);

    return (
      <>
        <div>{user.name}({user.age})의 장바구니(<Button size='sm' onClick={()=>{
                      dispatch(changeName()) // kim -> john kim
                  }}>ch user</Button> <Button size='sm' onClick={()=>{
                      dispatch(increAge(10)) // kim -> john kim
                  }}>+10</Button> <Button size='sm' onClick={()=>{
                      dispatch(increAge(1)) // kim -> john kim
                  }}>+1</Button>  <Button size='sm' onClick={()=>{
                      dispatch(decreAge(10)) // kim -> john kim
                  }}>-10</Button> <Button size='sm' onClick={()=>{
                      dispatch(decreAge(1)) // kim -> john kim
                  }}>-1</Button>)
        </div>
<Table striped bordered hover>
  <thead>
    <tr>{/* <tr> 가로줄 생김 */}
      <th>#[pid]</th>{/* <th> 헤드 컬럼 */}
      <th>상품명</th>
      <th>수량</th>
      <th>가격</th>
    </tr>
  </thead>
  <tbody>
  { 
        cart.map( (item, index) => // { return () } 생략가능
                <tr key={index}>
                  <td>{index+1}[{item.id}]</td>
                  <td>{item.name}</td>
                  <td>{item.count} <Button  size='sm' onClick={()=>{
                      dispatch(increment(item.id)) // count up ++
                      }}>+</Button><Button  size='sm' onClick={()=>{
                      dispatch(decrement(item.id)) // count up ++
                      }}>-</Button><Button  size='sm' onClick={()=>{
                      dispatch(delItem(item.id)) // count up ++
                      }}>Del</Button>
                  </td>
                  <td>{item.price}</td>
                </tr>
        )
  }
  </tbody>
  <tfoot>
        <tr>
          <td>-</td>
          <td>-</td>
          <td>합계</td>
          <td>-</td>
        </tr>
    </tfoot>
</Table>
        </>
    )
}

export default Cart;