import { configureStore, createSlice } from '@reduxjs/toolkit'

let user = createSlice({
    name: 'user',
    initialState: {name:'kim',age:20},
    reducers: {
        changeName(state) { 
            //return 'john'+state 
            //return {name:'park',age:20}
            //return state.name='park'
            return {...state,name:'park',age:30}
        },
        increAge(state,a) { 
            //return 'john'+state 
            //return {name:'park',age:20}
            //return state.age++
            return {...state,age:state.age+a.payload} // a's is value// payload : 인자값 // 화물. 소포. 
        },
        decreAge(state,a) { 
            //return 'john'+state 
            //return {name:'park',age:20}
            //return state.age++
            return {...state,age:state.age-a.payload}
        },
        setUser: (state, action) => action.payload,
    },
})

// for using function at exteranl reducer
export let {changeName,increAge,decreAge,함수2,함수3} = user.actions
//export let {changeName,increAge,decreAge,setUser} = user.actions
export default user