  function saveProductId(){
    console.log('@@@@@@@  @param_id.id=',param_id.id);
    let watcheds = [];
    let watched;
    console.log('@@@@@@@@ current localStorag=',localStorage.getItem('watched'));
    if(localStorage.getItem('watched') !== null && localStorage.getItem('watched') !== '' ){ // null or 빈값이 아닐때 watcheds에 watched 담기
      console.log('@@@@@ 빈값이 아닐때 localStorage(watched)=',localStorage.getItem('watched'));
      watched = localStorage.getItem('watched');
      watcheds.unshift(watched); // watcheds 에 기존 localStorage 데이터 입력
      console.log('@@ @@ pushed!!watcheds.push(\'new whatched !!\')');
    }
    console.log('@@@@@@@@ before!! localStorage.getItem(watched) =',localStorage.getItem('watched'));
    
    // 중복시 추가하지 않는다. param_id.id가 공백값이면 추가하지 않는다.
    // 중복시 해당값을 제일 앞(최근 데이터로 갱신)에 둔다. 중복시 추가하지 않되, 제일 앞에 둔다.

    // *******************************************************
    // unshift/ push- 배열의 시작/끝에 요소 추가
    // shift/ pop - 배열의 첫 번째/마지막 요소를 제거하고 반환
    // *******************************************************

    if(watcheds.indexOf(param_id.id)===-1 && param_id.id!==''){ 
      watcheds.unshift(param_id.id);
      console.log('@@@@@@ push new!! watcheds.push(param_id.id)');
    } 
    console.log('@@@@@@@@ after!! watcheds = localStorage.getItem(watched)=',watcheds);
    //localStorage.setItem('watched', JSON.stringify(watcheds)); // watcheds 를 JSON.stringify로 저장 
    localStorage.setItem('watched',watcheds); // 배열
    console.log('@@@@@@@@ watcheds 를 localStorage.setItem(watched) watcheds=',watcheds);   

    console.log('@@@@@@@@ @Show!! added new localStorage.getItem(watched)=',localStorage.getItem('watched'));


    localStorage.setItem('watched',localStorage.getItem('watched'));
    //watcheds = JSON.parse(localStorage.getItem('watched'));
    
    console.log('@@@@@@@@ @중복값 제거된 localStorage 보기!! added new localStorage.getItem(watched)=',localStorage.getItem('watched'));
    console.log('@@@@@@@@ @JSON.stringfy 중복값 제거된  localStorage 보기!! added new localStorage.getItem(watched)=',JSON.stringify(localStorage.getItem('watched')));



    localStorage.setItem('watched',localStorage.getItem('watched'));
    console.log('@@@@@@@@ @ localStorage 보기!! localStorage.setItem(watched,localStorage.getItem(watched))=',localStorage.getItem('watched'));
    //watcheds = JSON.parse(localStorage.getItem('watched'));

    // JSON.stringify로 변환해서 변수에 저장 후
    // , 로 구별하여 배열로 변환하여 저장
    // 배열로 만든후 중복 제거 함수 사용!!
    localStorage.setItem('watched',localStorage.getItem('watched').split(',').filter(function(v,i,a){return a.indexOf(v)===i;}).join(','));
    // 중복값 제거 후 다시 저장

    console.log('@@@@@@@@ 요기!! localStorage.getItem(\'watched\')=',localStorage.getItem('watched'));

    // watcheds가 배열일 때
    console.log('@@@@@@@@ typeof(watcheds) = ',typeof(watcheds));

    console.log('@@@@@@@@ @Last!! localStorage 보기!! localStorage.getItem(watched)=',localStorage.getItem('watched'));

  } 

