function Col(props) {
  return (
    // Receive shoes data from props
    // Receive i from props
    console.log('@@ props.shoes=',props.shoes),
    console.log('@@ props.i=',props.i),    
    // 배열.map(item,i),   
    // item.id, item.title, item.content,...,
    // <div className="col-12 p-1 col-xs-6 p-xs-6  col-sm-6 p-sm-6 col-md-4 p-md-4 col-lg-4 p-lg-4 py-3" key={props.i}>
    <div className="col" key={props.i}>
    {/* when using local public folder : img image folder */}
    {/* <img src={process.env.PUBLIC_URL+'/img/shoe'+(props.shoes.id+1)+'.jpg'} className="img-thumbnail" alt="..." width="80%"></img> */}
    <Link to={'/detail/'+props.shoes.id} className='px-3'><img src={'https://codingapple1.github.io/shop/shoes'+(props.shoes.id+1)+'.jpg'} className="img-thumbnail" alt="..." width="80%"></img></Link>
    <div className="pt-3 pb-3 text-center">
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
      <p>{numberFormat(props.shoes.price)}원</p>
      <Link to={'/detail/'+props.shoes.id} className='px-3'><button className="btn btn-outline-info" >상세보기</button></Link>
      </div>
    </div>
  ) // end of return
} // end of Col
